import React, { useState, useRef, useImperativeHandle, forwardRef, useEffect, useMemo, useCallback } from 'react';
import Table from "react-bootstrap/Table"
import { useForm, FormProvider, useFieldArray } from 'react-hook-form';
import classes from "./servicetable.module.scss";
import CommonCheckBox from 'components/UI/common/CheckBox/CommonCheckBox';
import TableBody from './TableBody';
import DeleteModal from 'components/UI/common/DeleteModal';
import {
  deleteServicesAmenities
} from "service/serviceAmenity";

const ServiceTable = forwardRef(
  (
    {
      tabValue,
      activeTabLabel,
      commonSelectAll,
      setCommonSelectAll,
      data = [],
      frequencyList = [],
      triggerAppend,
      setTriggerAppend,
      checkboxdata = [],
      handleDeleteArray,
      setFrequencyList,
      saveDraft,
      selectedFrequency,
      filterUpdate = () => { },
      setShowWarning,
      setIsDirty,
      showPreview
    },
    ref
  ) => {
    const tableRowRef = useRef([]);
    const [deleteRow, setDeleteRow] = useState(null);
    const [selectAll, setSelectAll] = useState(checkboxdata);

    const {
      control,
      formState: { errors, isDirty },
      setValue,
      register,
      trigger,
      handleSubmit,
      watch,
      ...restMethods
    } = useForm({
      defaultValues: {
        form: data,
      },
      mode: "onChange"
    });

    const prefillCheckbox = (data) => {
      const newCheckboxData = { ...checkboxdata };
      data.forEach((item1, i) => {
        const { type, roomType } = item1;
        const retailShortStay = item1.data?.length ? item1.data.every(o => o?.retailShortStay) : false
        const retailLongStay = item1.data?.length ? item1.data.every(o => o?.retailLongStay) : false
        const corporateShortStay = item1.data?.length ? item1.data.every(o => o?.corporateShortStay) : false
        const corporateLongStay = item1.data?.length ? item1.data.every(o => o?.corporateLongStay) : false
        if (newCheckboxData?.[type]?.[roomType.toLowerCase()]) newCheckboxData[type][roomType.toLowerCase()].retailShortStay = Boolean(retailShortStay)
        if (newCheckboxData?.[type]?.[roomType.toLowerCase()]) newCheckboxData[type][roomType.toLowerCase()].retailLongStay = Boolean(retailLongStay)
        if (newCheckboxData?.[type]?.[roomType.toLowerCase()]) newCheckboxData[type][roomType.toLowerCase()].corporateShortStay = Boolean(corporateShortStay)
        if (newCheckboxData?.[type]?.[roomType.toLowerCase()]) newCheckboxData[type][roomType.toLowerCase()].corporateLongStay = Boolean(corporateLongStay)
      })
      setSelectAll(newCheckboxData)
    }

    const prefillSelectAll = (data) => {
      let newCommonSelectAll = { ...commonSelectAll }
      data.forEach((item1, i) => {
        newCommonSelectAll[item1?.type][item1?.bsgp] = false;
        const availableStays = [];
        if (item1.retailLongStay) availableStays.push("retailLongStay")
        if (item1.retailShortStay) availableStays.push("retailShortStay")
        if (item1.corporateLongStay) availableStays.push("corporateLongStay")
        if (item1.corporateShortStay) availableStays.push("corporateShortStay")
        if (item1?.data?.length && availableStays?.length) {
          newCommonSelectAll[item1?.type][item1?.bsgp] = availableStays.every(stay => item1.data.every(a => a[stay]))
        }
      })
      setCommonSelectAll(newCommonSelectAll)
    }

    const handleDeleteRow = useCallback(
      (value, activeTab, i, id, parentIndex, watchField) => {
        setDeleteRow({
          tabValue: value,
          index: i,
          activeTabLabel,
          id,
          parentIndex,
          field: watchField
        });
        setTimeout(() => trigger(`form`), 0);
      },
      [activeTabLabel]
    );

    const { fields } = useFieldArray({
      control,
      name: "form",
    });

    useEffect(() => {
      setIsDirty?.(isDirty);
    }, [isDirty])

    const watchfields = watch("form");
    const setChecked = (isChecked) => {

      let newSelectAll = { ...selectAll };
      for (const property in newSelectAll[tabValue][activeTabLabel]) {
        newSelectAll[tabValue][activeTabLabel][property] = isChecked
      }

      setSelectAll(newSelectAll);

      watchfields.forEach((field, i) => {
        if (field.bsgp === activeTabLabel && field.type === tabValue) {
          field.data.forEach((nesField, j) => {
            setValue(`form.${i}.data.${j}.retailLongStay`, isChecked);
            setValue(`form.${i}.data.${j}.retailShortStay`, isChecked);
            setValue(`form.${i}.data.${j}.corporateLongStay`, isChecked);
            setValue(`form.${i}.data.${j}.corporateShortStay`, isChecked);
          });
        }
      });
      setTimeout(() => trigger("form"), 0)

      const setDetriminate = (name) => {
        const ele = document.getElementsByName(name);
        if (ele && ele[0]) ele[0].indeterminate = false;
      };

      setDetriminate("retailLongStay");
      setDetriminate("retailShortStay");
      setDetriminate("corporateLongStay");
      setDetriminate("corporateShortStay");
    };

    const onPressDelete = useCallback(() => {
      if (deleteRow?.parentIndex !== undefined) {
        tableRowRef.current[deleteRow.parentIndex]?.handleRowDelete(
          deleteRow.index
        );
      }
      if (deleteRow.id) {
        deleteServicesAmenities({ id: deleteRow.id })
        handleDeleteArray(deleteRow.id);
        const getActiveFields = watchfields?.find?.(a => a?.bsgp === deleteRow?.activeTabLabel && a?.type === deleteRow?.tabValue)?.data
        if (getActiveFields?.length && selectAll?.[tabValue]?.[activeTabLabel]) {
          let availableStays = [];
          Object.entries(selectAll[tabValue][activeTabLabel]).map(([key, value]) => {
            const onlyAvailableStays = getActiveFields?.filter(a => {
              if (a?.hasOwnProperty?.(key) && a?._id != deleteRow?.id) {
                availableStays.push(key)
                return true;
              }
              return false;
            });
            selectAll[tabValue][activeTabLabel][key] = onlyAvailableStays?.length ? onlyAvailableStays.every(a => Boolean(a?.[key])) : value
          })
          if (availableStays?.length) {
            commonSelectAll[tabValue][activeTabLabel] = availableStays.every(a => Boolean(selectAll[tabValue][activeTabLabel][a]))
            setCommonSelectAll(commonSelectAll);
          }
          setSelectAll(selectAll);
        }
      }
      // TODO: Need to check selectAll and header checkbox
      const getActiveTabObj = watchfields.find(field => (field?.bsgp === activeTabLabel && field?.type === tabValue));
      if (getActiveTabObj) {
        const selectedStays = ["corporateLongStay", "corporateShortStay", "retailLongStay", "retailShortStay"].filter(stay => getActiveTabObj[stay]);
        const newSelectAllData = { ...selectAll };
        if (getActiveTabObj?.data?.length) {
          selectedStays.forEach(stay => {
            newSelectAllData[tabValue][activeTabLabel][stay] = getActiveTabObj?.data?.every(d => d[stay]);
            const el = document.getElementsByName(stay);
            if (newSelectAllData[tabValue][activeTabLabel][stay] || getActiveTabObj?.data?.every(d => !d[stay])) {
              if (el?.[0]) el[0].indeterminate = false
            } else {
              if (el?.[0]) el[0].indeterminate = true
            }
          })
          setSelectAll({ ...newSelectAllData })
          setCommonSelectAll(prev => ({
            ...prev,
            [tabValue]: {
              ...prev[tabValue],
              [activeTabLabel]: selectedStays.every(stay => newSelectAllData[tabValue][activeTabLabel][stay])
            }
          }))
        }
      }
      closeModal();
    }, [deleteRow]);

    const closeModal = useCallback(() => {
      setDeleteRow(null);
    }, []);

    useImperativeHandle(ref, () => ({
      methods: {
        handleSubmit,
        reset: restMethods.reset,
        unregister: restMethods.unregister,
        setValue,
        watch
      },
      setChecked,
      prefillCheckbox,
      prefillSelectAll
    }));

    const handleCheckBox = (e, item, i) => {
      const { checked } = e.target;
      watchfields.forEach((field, i) => {
        if (field.type === tabValue && field.bsgp === activeTabLabel) {
          field.data.forEach((nesField, j) => {
            setValue(`form.${i}.data.${j}.${item.key}`, checked);
          });
        }
      });
      trigger(`form`)
      const newSelectAllData = { ...selectAll };
      newSelectAllData[tabValue][activeTabLabel][item.key] = !newSelectAllData[tabValue][activeTabLabel][item.key];
      setSelectAll({ ...newSelectAllData });
      if (checked) {
        setCommonSelectAll(prev => {
          const getActiveTabObj = watchfields.find(field => (field?.bsgp === activeTabLabel && field?.type === tabValue));
          const returnObj = {
            ...prev,
            [tabValue]: {
              ...prev[tabValue],
              [activeTabLabel]: Object.values(newSelectAllData?.[tabValue]?.[activeTabLabel])?.every(a => a)
            }
          };
          if (getActiveTabObj) {
            const selectedStays = ["corporateLongStay", "corporateShortStay", "retailLongStay", "retailShortStay"].filter(stay => getActiveTabObj[stay]);
            returnObj[tabValue][activeTabLabel] = selectedStays.every(stay => newSelectAllData[tabValue][activeTabLabel][stay])
          }
          return returnObj;
        })
      } else {
        setCommonSelectAll(prev => ({
          ...prev,
          [tabValue]: {
            ...prev[tabValue],
            [activeTabLabel]: false
          }
        }))
      }
    };
    return (
      <div className={classes.tableWrap}>
        {fields.map((field, i) => {
          const show = field.bsgp === activeTabLabel && field.type === tabValue;

          return (
            <>
              <Table
                key={field.id}
                className={`${classes.table} ${show ? "" : "d-none"}`}
              >
                <thead>
                  <tr>
                    {headers?.[tabValue]?.map((item, i) => {
                      const checkStayType =
                        (item?.key === "retailLongStay" &&
                          field?.retailLongStay) ||
                        (item?.key === "retailShortStay" &&
                          field?.retailShortStay) ||
                        (item?.key === "corporateShortStay" &&
                          field?.corporateShortStay) ||
                        (item?.key === "corporateLongStay" &&
                          field?.corporateLongStay);

                      if (
                        item?.key !== "retailLongStay" &&
                        item?.key !== "retailShortStay" &&
                        item?.key !== "corporateShortStay" &&
                        item?.key !== "corporateLongStay"
                      ) {
                        return (
                          <th key={item?.key}>
                            <div className={classes.wrapth}>{item.label}</div>
                          </th>
                        );
                      }
                      if (checkStayType) {
                        return (
                          <th key={item.key}>
                            <div className={classes.wrapth}>
                              {!showPreview && <CommonCheckBox
                                customClass={classes.checkbox}
                                onChange={(e) => handleCheckBox(e, item, i)}
                                isChecked={
                                  selectAll?.[tabValue]?.[activeTabLabel]?.[item.key]
                                }
                                name={item.key}
                              />}
                              {item.label}
                            </div>
                          </th>
                        );
                      }
                    })}
                  </tr>
                </thead>
                <tbody
                  className={`${show ? "" : "d-none"}`}
                >
                  <TableBody
                    ref={(el) => (tableRowRef.current[i] = el)}
                    tabValue={tabValue}
                    handleDelete={handleDeleteRow}
                    tab={activeTabLabel}
                    setCommonSelectAll={setCommonSelectAll}
                    setSelectAll={setSelectAll}
                    selectAll={selectAll}
                    control={control}
                    register={register}
                    trigger={trigger}
                    setValue={setValue}
                    errors={errors}
                    restMethods={restMethods}
                    index={i}
                    commonSelectAll={commonSelectAll}
                    watch={watch}
                    roomTypeId={field?.roomTypeId}
                    propertyId={field?.propertyId}
                    frequencyList={frequencyList}
                    triggerAppend={triggerAppend}
                    setTriggerAppend={setTriggerAppend}
                    retailLongStay={field?.retailLongStay || false}
                    retailShortStay={field?.retailShortStay || false}
                    corporateShortStay={field?.corporateShortStay || false}
                    corporateLongStay={field?.corporateLongStay || false}
                    setFrequencyList={setFrequencyList}
                    saveDraft={saveDraft}
                    showPreview={showPreview}
                  />
                </tbody>
              </Table>
              {show && watch(`form.${i}.data`)?.length == 0 && <div className="d-flex justify-content-center w-100">
                No Data found
              </div>}
            </>
          );
        })}
        {deleteRow && (
          <DeleteModal
            description={`Are you sure you want to delete the frequency type ${deleteRow?.field?.frequency
              ? `“${deleteRow?.field?.frequency || ""}”`
              : ""
              } for ${deleteRow?.field?.name || ""} ${deleteRow?.tabValue === "services" ? "Service" : "Amenity"
              }?`}
            handleDelete={onPressDelete}
            handleClose={closeModal}
            visible
          />
        )}
      </div>
    );
  }
);

ServiceTable.displayName = "ServiceTable"

export default ServiceTable;

const headers = {
  services: [
    { key: "service", label: "Service" },
    { key: "frequency", label: "Frequency" },
    { key: "chargingType", label: "Charging Type" },
    { key: "rate", label: "Rate (₹)" },
    {
      key: "retailShortStay",
      label: "Retail Short Stay",
      showCheckbox: true,
      selectAll: false
    },
    {
      key: "retailLongStay",
      label: "Retail Long Stay",
      showCheckbox: true,
      selectAll: false
    },
    {
      key: "corporateShortStay",
      label: "Corporate Short Stay",
      showCheckbox: true,
      selectAll: false
    },
    {
      key: "corporateLongStay",
      label: "Corporate Long Stay",
      showCheckbox: true,
      selectAll: false
    },
    { key: "action", label: "" },
  ],
  amenities: [
    { key: "amenities", label: "Amenity" },
    { key: "frequency", label: "Frequency" },
    { key: "chargingType", label: "Charging Type" },
    { key: "rate", label: "Rate (₹)" },
    {
      key: "retailShortStay",
      label: "Retail Short Stay",
      showCheckbox: true,
    },
    {
      key: "retailLongStay",
      label: "Retail Long Stay",
      showCheckbox: true,
    },
    {
      key: "corporateShortStay",
      label: "Corporate Short Stay",
      showCheckbox: true,
    },
    {
      key: "corporateLongStay",
      label: "Corporate Long Stay",
      showCheckbox: true,
    },
    { key: "action", label: "" },
  ]
}

const deepClone = (json) => {
  return JSON.parse(JSON.stringify(json))
}

